@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700;800&display=swap);
*{padding:0;margin:0;box-sizing:border-box}body{padding:0;margin:0;background-color:#fff}:root{--clr-green: #6CD086;--clr-deep-blue: #1F2C8C;--clr-deep-blue-dark: #2e4d8b;--clr-deep-blue-light: #C9D0FF}@media (min-width: 1400px){.container{max-width:1140px !important}}.section-padding{padding-top:100px;padding-bottom:100px}.bg-clr-green{background-color:#6CD086 !important;background-color:var(--clr-green) !important}.bg-clr-deep-blue{background-color:#1F2C8C !important;background-color:var(--clr-deep-blue) !important}.bg-clr-deep-blue-light{background-color:#C9D0FF !important;background-color:var(--clr-deep-blue-light) !important}.bg-clr-deep-blue-dark{background-color:#2e4d8b !important;background-color:var(--clr-deep-blue-dark) !important}.btn-cryptic{transition:all .3s;font-weight:500 !important}.btn-cryptic.bg-clr-green:hover{background-color:#C9D0FF !important;background-color:var(--clr-deep-blue-light) !important}.btn-cryptic.bg-clr-deep-blue-light:hover{background-color:#6CD086 !important;background-color:var(--clr-green) !important}.btn-cryptic.bg-clr-deep-blue-dark:hover{background-color:#6CD086 !important;background-color:var(--clr-green) !important}

#carousel{background-color:var(--clr-deep-blue)}#carousel .carousel-control{width:45px;height:45px;border-radius:100%;display:grid;place-items:center;background-color:var(--clr-green) !important;color:#fff !important;position:absolute;top:50%;-webkit-transform:translateY(-50%) rotate(45deg);transform:translateY(-50%) rotate(45deg);outline:none;border:0;box-shadow:none;transition:all .3s;opacity:0}#carousel .carousel-control:hover{background-color:var(--clr-deep-blue-light) !important}#carousel .carousel-control#carousel-back{right:auto;right:initial;left:30px}#carousel .carousel-control#carousel-next{left:auto;left:initial;right:30px}#carousel .carousel-height{--height: 800px;height:var(--height);min-height:100vh}#carousel:hover .carousel-control{-webkit-transform:translateY(-50%) rotate(0deg);transform:translateY(-50%) rotate(0deg);opacity:1}#carousel:hover .carousel-control#carousel-back{right:auto;right:initial;left:100px !important}#carousel:hover .carousel-control#carousel-next{left:auto;left:initial;right:100px !important}

.presale-listing{background-color:#F7F7F7}.presale-listing .nav-pills .nav-link{font-size:16px;border:2px solid #ECECEC;text-align:center;border-radius:30px;margin:3px 10px;font-weight:500;transition:all .3s}.presale-listing .nav-pills .nav-link.active{background-color:var(--clr-deep-blue);border-color:var(--clr-deep-blue);color:var(--bs-white)}.presale-listing .nav-pills .nav-link:hover{background-color:var(--clr-deep-blue);color:var(--bs-white)}.listing-card{padding:30px;border-radius:10px;margin-bottom:25px}.listing-card .cardHead>img{width:45px;height:45px;border-radius:100%;object-fit:cover;object-position:center}.listing-card .cardHead>.title{font-size:calc(var(--fontInitSize) + 10px);font-weight:700}.listing-card .cardBottom span{font-size:calc(var(--fontInitSize) - 2px);font-weight:500;color:var(--themeGray)}

.navbar{position:absolute !important;top:0;left:0;right:0;width:100%;z-index:100}.navbar .navbar-nav li.nav-item>a.nav-link{font-weight:400;line-height:26px;text-align:left;padding:0;color:#fff;position:relative;font-size:14px;font-family:'Montserrat', sans-serif;transition:all .3s}.navbar .navbar-nav:not(.navbar-auth) li.nav-item>a.nav-link::before{content:'';display:block;width:0px;height:2px;position:absolute;left:0;top:100%;transition:all .3s;opacity:0;background-color:var(--clr-green)}.navbar .navbar-nav:not(.navbar-auth) li.nav-item>a.nav-link:hover{color:var(--clr-green)}.navbar .navbar-nav:not(.navbar-auth) li.nav-item>a.nav-link:hover::before{width:20px;opacity:1}

.is-bg-img{background-repeat:no-repeat;background-position:center bottom}.btn-submit{position:absolute;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%);right:5px;background-color:var(--clr-deep-blue) !important;border-color:var(--clr-deep-blue) !important}.btn-submit:hover{background-color:var(--clr-green) !important;border-color:var(--clr-green) !important}.opera-item{transition:all .3s linear;border:1px solid rgba(0,0,0,0.2) !important}.opera-item:hover{border-color:var(--clr-deep-blue) !important}.opera-item img.opera-img{height:275px;width:100%;object-fit:cover;object-position:top}.pagination li a{width:40px;height:40px;border:1px solid rgba(0,0,0,0.2) !important;display:grid;place-items:center;color:#000;text-decoration:none;transition:all .3s;margin:4px;border-radius:100%}.pagination li a:hover{background-color:#000;border-color:#000;color:#fff}.pagination li.active>a{background-color:var(--clr-deep-blue);border-color:var(--clr-deep-blue);color:#fff}

.cryptic-content{font-family:'Montserrat', sans-serif}.cryptic-content .title{font-size:36px;font-weight:700;line-height:calc(100% + 10px)}.cryptic-content .sub-title{font-size:17px;font-weight:400;line-height:calc(100% + 12px)}

.contact-form .form-control{border-color:transparent;min-height:50px;font-size:14px !important;font-weight:500}.contact-form .form-control:focus{border-color:var(--clr-deep-blue) !important;box-shadow:none}.contact-form .form-control.form-control-textarea{border-radius:20px !important}

.footer{background-repeat:no-repeat;background-size:cover;background-position:center;padding-top:120px;padding-bottom:170px;position:relative}.footer .footerGridBox .footerGridBoxTitle{margin-bottom:35px;font-size:22px;color:var(--bs-white);font-weight:700}.footer .footerGridBox .nav-item .nav-link{color:#c9d0ff;padding:0;padding-left:0;position:relative;transition:all .4s;display:inline-block;font-size:14px}.footer .footerGridBox .nav-item .nav-link::before{content:'';display:block;width:0px;height:2px;border-radius:10px;background-color:var(--bs-white);position:absolute;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%);left:0;transition:all .2s}.footer .footerGridBox .nav-item .nav-link:hover{padding-left:22px;color:var(--bs-white)}.footer .footerGridBox .nav-item .nav-link:hover::before{width:16px}.footer .footerGridBox .nav-item+.nav-item .nav-link{margin-top:15px}.footer .footerBottom{position:absolute;width:100%;bottom:0;left:0;background-color:rgba(0,0,0,0.2)}.footer .footerBottom p{font-size:14px;color:#fff;margin:0}

.pageBanner{background:#1f2c8c;background-position:center;background-size:cover;object-fit:cover;padding-top:120px;padding-bottom:120px}.pageBanner .pageBanner-content{text-align:center}.pageBanner .pageBanner-content .banner-title{font-weight:700;font-size:calc(1vw + 24px);line-height:42px;text-align:center;color:#fff;margin-bottom:15px}.pageBanner .pageBanner-content .breadcrumb{background-color:transparent}.pageBanner .pageBanner-content .breadcrumb li{font-weight:400}.pageBanner .pageBanner-content .breadcrumb li.active{color:var(--clr-green) !important}.pageBanner .pageBanner-content .breadcrumb li a{font-weight:400;font-size:16px;line-height:24px;text-align:center;text-decoration:none;color:#fff}.pageBanner .pageBanner-content .breadcrumb li a:hover{text-decoration:none}

